<script>
import { mapActions } from 'vuex'

export default {
  name: 'Timeline',
  props: {
    events: Object
  },
  methods: {
    ...mapActions([
      'activeSet'
    ]),
    getWidth (beg, finish) {
      var total = '540'
      var start = this.getAsMonth(beg)
      var end = this.getAsMonth(finish)
      var numMonths = (end[1] - start[1]) * 12 + (end[0] - start[0] + 1)
      var perc = (numMonths / total * 100).toFixed(2)
      return perc + '%'
    },
    getAsMonth (date) {
      var arr = date.split('-')
      switch (arr[0]) {
        case 'January':
          arr[0] = 1
          break
        case 'February':
          arr[0] = 2
          break
        case 'March':
          arr[0] = 3
          break
        case 'April':
          arr[0] = 4
          break
        case 'May':
          arr[0] = 5
          break
        case 'June':
          arr[0] = 6
          break
        case 'July':
          arr[0] = 7
          break
        case 'August':
          arr[0] = 8
          break
        case 'September':
          arr[0] = 9
          break
        case 'October':
          arr[0] = 10
          break
        case 'November':
          arr[0] = 11
          break
        case 'December':
          arr[0] = 12
          break
      }
      return arr
    }
  },
  computed: {
    active () {
      return this.$store.state.active
    }
  }
}
</script>

